const WxWebSocket = require('./WxWebSocket.js');
const Logger = require('./Logger.js');
const EnhancedEventEmitter = require('./EnhancedEventEmitter.js');
const config = require('../config.js');
const retry = require('../retry/retry.js');

const logger = new Logger('IPLocation');

// 
const MAX_RETRY = 10;

// IPLocation  timeout
const IP_FULL_TIMEOUT = 15000; // 15s

// 
// {
const IPL_STATE_IDLE = 0; // /
const IPL_STATE_STARTING = 1; // 
const IPL_STATE_ACT = 2; // /
const IPL_STATE_RETRYING = 3; // 
// }

//  / retry
const DEFAULT_RETRY_OPTIONS =
{
	retries    : MAX_RETRY, // 
	factor     : 2, // 
	minTimeout : 1 * 1000, // 
	maxTimeout : 8 * 1000
};

// TODO : retry while connect fail or no-response 
//  retry 

class IPLocation extends EnhancedEventEmitter
{
	/**
	 * @param {Object} [options] - Options for WebSocket-Node.W3CWebSocket and retry.
	 */
	constructor(options)
	{
		super(logger);
		
		// Options.
		// @type {Object}
		this._options = options || {};

		// 
		this._logClues = options.logClues;

		// 
		this._sid = options.sid;

		// WebSocket instance.
		// @type {WebSocket}
		this._ws = null;

		this._operation = null;

		// Map of pending sent request objects indexed by request id.
		// @type {Map<Number, Object>}
		this._sents = new Map();

		//  close ?
		this._stopping = false;

		// 
		this._state = IPL_STATE_IDLE;

		this._iplocTimer = null;
		//  got
		this._hasGot = false;

		// 
		this._isTPFinish = false;

		// 
		this._randomId = 0;
	}

	async init(opts)
	{
		if (this._state !== IPL_STATE_IDLE)
			return;
		
		const {
			url,
			appId,
			userId
		} = opts;

		this._url = url;
		this._appId = appId;
		this._userId = userId;

		try
		{
			this._hasGot = false;

			// 
			this._stopping = false;

			this._state = IPL_STATE_STARTING;

			// 
			let iplocProc = new Promise((resolve, reject) =>
			{
				// 
				this.runIploc();
			});

			let iplocTimerPro = new Promise((resolve, reject) =>
			{
				this._iplocTimer = setTimeout(() =>
				{
					reject({
						code   : 500,
						reason : 'iplocation timeout'
					});
				}, IP_FULL_TIMEOUT);
			});

			Promise.race([ iplocProc, iplocTimerPro ])
				.then((result) =>
				{
				})
				.catch((e) =>
				{
					this.close();

					this.safeEmit('failed', {
						sid    : this._sid,
						code   : 500,
						reason : 'iplocation FIN failed'
					});
				});
			// 
		}
		catch (error)
		{
			throw new Error(error);
		}
	}

	onMessage(resp)
	{
		//  got resolve reject
		this._hasGot = true;

		// 
		clearTimeout(this._iplocTimer);

		// 
		const sent = this._sents.get(1);

		if (!sent)
		{
			logger.error(
				'IPLocation onMessage received response does not match any sent request [id:%s]',
				1);

			return;
		}

		sent.resolve(resp);
	}

	async request(opts)
	{
		const {
			host,
			appId,
			userId,
			sid
		} = opts;

		// 
		try
		{
			if (Boolean(this._ws) && this._state === IPL_STATE_ACT)
			{
				// 
				const iplocationData = {
					eType        : 1,
					sSEQID       : '111111111',
					mProtocolMsg : {
						sVersion     : 'Test_4.2.0.777',
						bLoadBalance : false,
						bReConnect   : false,
						bVipUser     : false,
						sIDentify    : '7B4FCDBC0914E7182B8EC357F07498F1',
						sSecret      : 'c052cd055acfabc5',
						sVerifyCode  : 'abcd',
						sAppID       : appId,
						nUserID      : parseInt(userId),
						sClientVer   : 'miniapp_1.0',
						sConnectID   : sid,
						mIpAddrMsg   : {
							bFec  : false,
							sIp   : host,
							nPort : 0
						}
					}
				};

				logger.debug(`IPLocation request() ws.send: ${JSON.stringify(iplocationData)}`);

				this._ws.send({ msg: JSON.stringify(iplocationData) });
			}
		}
		catch (error)
		{
			logger.error('IPLocation request() ws.send failed. error: ', error);

			return Promise.reject(new Error('WebSocket send failed'));
		}

		// 
		//  sents  idmethod  sents  IPLoc 
		// 
		return new Promise((resolve, reject) =>
		{
			const requestId = 1;
			const method = 'request';
			const timeout = 1500 * (15 + (0.1 * this._sents.size));
			const sent =
			{
				id      : requestId, // 1
				method  : method,
				resolve : (data2) =>
				{
					if (!this._sents.delete(1))
					{
						return;
					}

					clearTimeout(sent.timer);
					
					logger.debug('IPLocation request[method: ', method, '] resolve. response: ', data2);

					resolve(data2);
				},
				reject : (error) =>
				{
					logger.debug(`IPLocation request[method: ${method}] reject.`);

					if (!this._sents.delete(requestId))
						return;

					clearTimeout(sent.timer);
					reject(error);
				},
				timer : setTimeout(() =>
				{
					logger.error('IPLocation request[method: %s] reject.', method);

					if (!this._sents.delete(requestId))
						return;

					reject(new Error('Request Timeout.'));
				}, timeout),
				close : () =>
				{
					logger.error('IPLocation request[method: %s] close.', method);

					clearTimeout(sent.timer);
					reject(new Error('Peer Closed'));
				}
			};

			// Add sent stuff to the map.
			this._sents.set(requestId, sent);
		});
	}

	// 
	// N ""
	//  "close"
	onWsClose(opts)
	{
		// 
		if (this._hasGot)
		{
			return;
		}

		// 
		const { event, currentAttempt } = opts;

		// Don't retry if code is 4000 (closed by the server).
		// 4000 -- closed by server
		// 1000 -- normal closure
		if (event.code !== 4000 && event.code !== 1000)
		{
			if (this._stopping) // 
			{
				logger.debug(`IPLocation onWsClose() -- [event.code:${event.code}] [this._state: ${this._state}]`);

				// 
				this._operation.stop();

				/* 2019.7.18 wxwebsocketcloseTransport
				this.closeTransport();
				*/

				this._state = IPL_STATE_IDLE;

				this.safeEmit('close', {
					sid : this._sid
				});

				return;
			}
			else if (this._state !== IPL_STATE_ACT) // 
			{
				this._state = IPL_STATE_RETRYING; // 

				if (this._operation.retry(true))
				{
					logger.debug(`IPLocation onWsClose() -- [event.code: ${event.code}]. retrying.`);

					// 
					this.safeEmit('auto-reconnecting', {
						sid : this._sid
					});
				}
				else
				{
					logger.error(`IPLocation onWsClose() -- [event.code: ${event.code}]. FINAL FAILED.`);

					// 
					this._operation.stop();
					// 
					this._state = IPL_STATE_IDLE;
					//  ok failed event:{code: 500}
					this.safeEmit('failed', {
						sid    : this._sid,
						code   : 500,
						reason : 'iplocation FIN failed'
					});
				}

				return;
			}
			// ??
			else if (this._state === IPL_STATE_ACT)
			{
				logger.debug(`IPLocation onWsClose() -- [event.code: ${event.code}] [this._wstState: IPL_STATE_ACT]. so FULL-reconnect.`);

				// 
				this._operation.stop();
				// 
				this._state = IPL_STATE_RETRYING; // 

				// 
				this.safeEmit('auto-reconnecting', {
					sid : this._sid
				});

				this.runIploc();
			}
			else
			{
				logger.debug(`IPLocation onWsClose() -- [event.code: ${event.code}] [this._wstState: IPL_STATE_ACT]. so FULL-reconnect.`);
			}
		}
		else
		{
			logger.debug(`IPLocation onWsClose() -- [event.code: ${event.code}]. this._state: ${this._state}. stop.`);

			// 
			this._operation.stop();

			// 
			/* 2019.7.18 wxwebsocketcloseTransport
			this.closeTransport();
			*/

			this._state = IPL_STATE_IDLE;

			//  'close' 
			// Emit 'close' event.
			this.safeEmit('close', {
				sid : this._sid
			});
		}
	}

	async runIploc()
	{
		this._operation =
			retry.operation(this._options.retry || DEFAULT_RETRY_OPTIONS);

		if (!this._operation)
		{
			logger.error('IPLocation runIploc() Internal error.');

			throw new Error('Internal error');
		}

		logger.debug(`IPLocation runIploc attempt connect to : ${this._url}`);

		this._operation.attempt((currentAttempt) =>
		{
			// 
			this.closeTransport();
			// 

			//  close
			if (this._stopping)
			{
				this._operation.stop();

				return;
			}
			
			logger.debug(`IPLocation runIploc attempt connect to : ${this._url} currentAttempt: ${currentAttempt}`);

			// 
			this._randomId = Math.round(Math.random() * 10000000);

			// 
			this._ws = new WxWebSocket(this._randomId, logger);

			if (Boolean(this._ws))
			{
				//  wx websocket 
				this._ws.on('open', async () =>
				{
					//  close  return
					if (this._stopping)
					{
						logger.debug('IPLocation event[open] -- open after stopping.');

						return;
					}
					// 
					this._state = IPL_STATE_ACT;
					// 

					logger.debug(`IPLocation ws event[open] -- this._state: ${this._state}`);
					
					// 
					const iplocRes = await this.request({
						host   : 'stech.3ttech.cn', //  
						appId  : this._appId,
						userId : this._userId,
						sid    : this.sid
					});

					// 
					logger.warn(`IPLocation -- result: ${JSON.stringify(iplocRes)}`);
					
					// 
					// miniappHome  miniapp au
					const miniappHome = (iplocRes.mProtocolMsg.mIpAddrMsg.sDomain);

					let auAccess = config.miniauHost;

					// 
					// 1.   wss://xxx.xxx.xxx/miniappau-${miniappHome} --  miniappHome  url 
					// xxx.xxx.xxx  nginx url path  ${miniappHome} proxy
					// 2.  miniappHome
					if (miniappHome != null && miniappHome !== '' && typeof miniappHome !== 'undefined')
					{
						// 1. 1 auAccess = `${config.miniauHost}-${miniappHome}`;
						// 
						// 2. 2 
						auAccess = `${miniappHome}`;
					}
					else
					{
						logger.error(`IPLocation -- error: miniappHome: ${miniappHome}`);

						this.safeEmit('failed', {
							sid    : this._sid,
							code   : 500,
							reason : `miniappHome: ${miniappHome}`
						});
					}

					//
					// 
					this._isTPFinish = true;

					this.safeEmit('ok', {
						sid : this._sid,
						auAccess
					});
				});

				// 
				this._ws.on('message', (event) =>
				{
					const resp = JSON.parse(event.data);

					// 
					this.onMessage(resp);
				});

				this._ws.on('error', (event) =>
				{
					logger.error('IPLocation ws event[error] -- this._state: ', this._state, ' event: ', event);

					//
				});

				this._ws.on('close', (event) =>
				{
					this.onWsClose({
						event,
						currentAttempt
					});
				});

				logger.debug(`IPLocation runIploc() [currentAttempt: ${currentAttempt}]. try to connect: ${this._url}`);

				// connect server
				this._ws.connect({
					url     : this._url,
					success : (res) => {},
					fail    : (res) =>
					{
						logger.error('IPLocation ws.connect failed. error: ', res);

						this.safeEmit('failed', {
							sid    : this._sid,
							code   : 500,
							reason : 'ws.connect failed'
						});
					}
				});
			}
			else
			{
				logger.error('IPLocation WebSocket create failed.');

				this.safeEmit('failed', {
					sid    : this._sid,
					code   : 500,
					reason : 'WebSocket create failed'
				});
			}
		});
	}

	closeTransport()
	{
		// Close every pending sent.
		for (const sent of this._sents.values())
		{
			sent.close();
		}

		const code = 1000;
		const reason = 'close connection';

		try
		{
			if (Boolean(this._ws))
			{
				this._ws.close({
					code,
					reason
				});
			}
		}
		catch (error)
		{
			logger.error(`IPLocation closeTransport() failed. Closing the WebSocket error: ${JSON.stringify(error)}`);
		}
	}
	
	close()
	{
		// Don't wait for the WebSocket 'close' event, do it now.
		this._stopping = true;

		logger.debug('IPLocation close()');

		if (this._state === IPL_STATE_IDLE)
		{
			return;
		}

		this.closeTransport();

		// 
		this._operation.stop();
		// 
		this._state = IPL_STATE_IDLE;
	}
}

module.exports = IPLocation;
