const Logger = require('./Logger.js');
const EnhancedEventEmitter = require('./EnhancedEventEmitter.js');

// 
// {
const WX_STATE_IDLE = 0; // /
const WX_STATE_STARTING = 1; // 
const WX_STATE_ACT = 2; // /
const WX_STATE_STOPING = 3; // 
// }

class WxWebSocket extends EnhancedEventEmitter
{
	constructor(randomId, logger)
	{
		super(logger);

		// 
		this._logger = logger || new Logger('WxWebSocket');
		this._logger.prefix = '-';

		// 
		this._connState = WX_STATE_IDLE;

		//  close ??
		this._stopping = false;

		// socket
		/* 
		this._msgQueue = [];
		*/

		//  -- 
		// this._connectSocketTimeOut = null;

		this._socketTask = null;
		// 
  
		// 
		this._randomId = randomId;
	}

	/**
	 *  WebSocket 
	 * @param {options} 
	 *   url          String         wss 
	 *   header        Object        HTTP Header , header  Referer
	 *   method        String        GETOPTIONS, GET, HEAD, POST, PUT, DELETE, TRACE, CONNECT
	 *   protocols    StringArray            1.4.0
	 *   success      Function        
	 *   fail         Function        
	 *   complete      Function        
	 */
	async connect(options)
	{
		this._stopping = false;

		//
		this._connState = WX_STATE_STARTING;

		/* 
		this._msgQueue = [];
		*/

		try
		{
			this._logger.debug(`WxWebSocket connect() -- wx.connectSocket url: ${options.url}`);

			/* eslint-disable no-undef */
			this._socketTask = wx.connectSocket({
			/* eslint-enable no-undef */
				url    : options.url,
				header : {
					'content-type' : 'application/json'
				},
				method  : 'GET',
				success : (res) =>
				{
					if (options)
					{
						// 
						options.success && options.success(res);
					}

					this._logger.debug('WxWebSocket connect() -- wx.connectSocket call OK.');
				},
				fail : (res) =>
				{
					if (options)
					{
						// 
						options.fail && options.fail(res);
					}

					this._connState = WX_STATE_IDLE;

					this._logger.warn('WxWebSocket connect() -- wx.connectSocket call FAIL.');
				},
				complete : (res) =>
				{
				}
			});
		}
		catch (error)
		{
			options.fail && options.fail('EXCEPTION');

			this._connState = WX_STATE_IDLE;

			this._logger.warn('WxWebSocket connect() -- wx.connectSocket call EXCEPTION.');

			return;
		}
		finally
		{
			// 
		}

		// 
		if (Boolean(this._socketTask))
		{
			this._socketTask.onClose((r) => this.onSocketClose(r));
			this._socketTask.onMessage((r) => this.onSocketMessage(r));
			this._socketTask.onError((r) => this.onSocketError(r));
			this._socketTask.onOpen((r) => this.onSocketOpen(r));
		}
	}

	/**
	 *  WebSocket 
	 * @param {options} 
	 *   data    String / ArrayBuffer        
	 *   success    Function        
	 *   fail    Function        
	 *   complete    Function        
	 */
	async send(options)
	{
		if (!options)
		{
			this._logger.debug('WxWebSocket send() invalid parameters -- options');

			return;
		}

		this._logger.debug(`WxWebSocket send() <<<<<< ${options.msg}`);

		if (Boolean(this._socketTask) && this._connState === WX_STATE_ACT)
		{
			this._socketTask.send({
				data    : options.msg,
				success : (res) =>
				{
					this._logger.debug('WxWebSocket send() -- call wx socketTask.send OK.');

					if (options)
					{
						options.success && options.success(res);
					}
				},
				fail : (res) =>
				{
					this._logger.debug('WxWebSocket send() -- call wx socketTask.send FAIL.');

					if (options)
					{
						options.fail && options.fail(res);
					}
				}
			});
		}
		else
		{
			this._logger.debug(`WxWebSocket send() error -- this._connState: ${this._connState}`);

			/* 
			this._msgQueue.push(options.msg);
			*/
		}
	}

	/**
	 *  WebSocket 
	 * @param {opts} 
	 *   code    Number        1000 
	 *   reason    String        123UTF-8 
	 *   fail    Function        
	 *   complete    Function        
	 */
	close(opts)
	{
		this._logger.debug('WxWebSocket close() -- will call closeInner().');

		this._stopping = true;

		this._connState = WX_STATE_STOPING;

		this.closeInner(opts);
	}

	closeInner(opts)
	{
		/*
		if (this._connectSocketTimeOut)
		{
			clearTimeout(this._connectSocketTimeOut);
			this._connectSocketTimeOut = null;
		}
		*/
		const {
			code,
			reason,
			success,
			fail
		} = opts;

		if (Boolean(this._socketTask) && (this._connState !== WX_STATE_IDLE))
		{
			this._logger.debug('WxWebSocket closeInner() -- call wx socketTask.close.');

			this._socketTask.close({
				code,
				reason,
				success,
				fail
				
				/*
				success : (res) =>
				{
					this._logger.debug('WxWebSocket socket closed.');
					if (opts)
					{
						opts.success && opts.success(res);
					}
				},
				fail : (res) =>
				{
					if (opts)
					{
						opts.fail && opts.fail(res);
					}
				}
				*/
			});
		}

		this._socketTask = null;
		this._connState = WX_STATE_IDLE;
	}

	// 
	// WebSocket
	onSocketOpen(res)
	{
		// function
		if (this._stopping)
		{
			this._logger.debug('WxWebSocket onSocketOpen() -- this._stopping is ture, will call closeInner().');
			this.closeInner({});
		}
		else
		{
			this._logger.debug('WxWebSocket onSocketOpen() -- will emit [open].');

			this._connState = WX_STATE_ACT;
			// 
			/* 
			for (let i = 0; i < this._msgQueue.length; i++)
			{
				this.send({ msg: this._msgQueue[i] });
			}
			this._msgQueue = [];
			*/

			this.safeEmit('open', {
				randomId : this._randomId
			});
		}
	}

	// WebSocket
	onSocketError(res)
	{
		this._logger.debug(`WxWebSocket onSocketError() -- this._stopping: ${this._stopping}`);

		this._connState = WX_STATE_IDLE;

		//   close 
		if (this._stopping)
		{
			/*
			// code : 1000 
			const evt = { wasClean: 'true', code: 1000, reason: res.errMsg };

			this.safeEmit('close', evt);
			*/
		}
		else
		{
			this.safeEmit('error', {
				randomId : this._randomId,
				res
			});
		}
	}

	// WebSocket
	onSocketMessage(event)
	{
		this._logger.debug(`WxWebSocket onSocketMessage() >>>>>> message: ${event.data}`);

		this.safeEmit('message', {
			randomId : this._randomId,
			data     : event.data
		});
	}

	// WebSocket
	onSocketClose(res)
	{
		/*
		if (this._connState !== WX_STATE_STOPING)
		{
			clearTimeout(this._connectSocketTimeOut);
			this._connectSocketTimeOut = setTimeout(() =>
			{
				this.connect();
			}, 3000);
		}
		*/

		this._logger.debug(`WxWebSocket onSocketClose() -- this._stopping: ${this._stopping}`);

		this._connState = WX_STATE_IDLE;

		if (this._stopping)
		{
			return;
		}

		this.safeEmit('close', {
			randomId : this._randomId,
			wasClean : 'true',
			code     : res.code,
			reason   : res.reason
		});
	}
}

module.exports = WxWebSocket;
