const debug = require('../thirdparty/debug.js');

const APP_NAME = 'tttminiappsdk';

const LOG_NONE = 0;
const LOG_ERROR = 1;
const LOG_WARN = 2;
const LOG_INFO = 3;
const LOG_DEBUG = 4;
const LOG_TRACE = 5;

class Logger
{
	constructor(prefix)
	{
		// 
		this._appId = '';
		this._roomId = '';
		this._userId = '';
		this._logClues = '';
		this._prefix = '';

		this._logLevel = LOG_DEBUG;

		// 
		if (prefix)
		{
			this._debug = debug(`${APP_NAME}:${prefix}`);
			this._warn = debug(`${APP_NAME}:WARN:${prefix}`);
			this._error = debug(`${APP_NAME}:ERROR:${prefix}`);

			// 
			this._prefix = prefix;
		}
		else
		{
			this._debug = debug(APP_NAME);
			this._warn = debug(`${APP_NAME}:WARN`);
			this._error = debug(`${APP_NAME}:ERROR`);
		}

		/* eslint-disable no-console */
		// this._debug.log = console.info.bind(console);
		// this._warn.log = console.warn.bind(console);
		// this._error.log = console.error.bind(console);
		/* eslint-enable no-console */
		// 
		this._debug = this._logDebug;
		this._warn = this._logWarn;
		this._error = this._logError;
	}

	set logLevel(logLevel)
	{
		switch (logLevel) {
			case 'LOG_NONE':
				this._logLevel = LOG_NONE;
				break;
			case 'LOG_ERROR':
				this._logLevel = LOG_ERROR;
				break;
			case 'LOG_WARN':
				this._logLevel = LOG_WARN;
				break;
			case 'LOG_INFO':
				this._logLevel = LOG_INFO;
				break;
			case 'LOG_DEBUG':
				this._logLevel = LOG_DEBUG;
				break;
			case 'LOG_TRACE':
				this._logLevel = LOG_TRACE;
				break;
			default:
				this._logLevel = LOG_DEBUG;
		}
	}

	get debug()
	{
		return this._debug;
	}

	get warn()
	{
		return this._warn;
	}

	get error()
	{
		return this._error;
	}

	set prefix(prefix)
	{
		if (!prefix)
		{
			return;
		}

		this._prefix = prefix;
	}

	setParameters(opts)
	{
		const {
			appId,
			roomId,
			userId,
			logClues
		} = opts;

		// 
		this._appId = appId;
		this._roomId = roomId;
		this._userId = userId;
		this._logClues = logClues;
	}

	// 
	_logDebug(...args)
	{
		this.postLog(LOG_DEBUG, ...args);
	}

	_logWarn(...args)
	{
		this.postLog(LOG_WARN, ...args);
	}

	_logError(...args)
	{
		this.postLog(LOG_ERROR, ...args);
	}

	postLog(logLevel, ...args)
	{
		// 
		const appId = this._appId;
		const roomId = this._roomId;
		const userId = this._userId;

		const preMsg = `appid=${appId}, roomid=${roomId}, userid=${userId} time=${new Date()} logClues=${this._logClues} <${this._prefix}>`;
		let msg = '';

		[].slice.apply(args).forEach((element) =>
		{
			if (typeof element === 'object')
			{
				const s = JSON.stringify(element);

				msg = `${msg} ${s}`;
			}
			else
			{
				msg = `${msg} ${element}`;
			}
		});

		wx.request({
			url    : 'https://log.wushuangtech.com:15100',
			method : 'POST',
			data   : `event=${APP_NAME} ${preMsg} ${msg}`,
			header : {
				'Content-Type' : 'text/plain; charset=utf-8'
			},
			success(res)
			{
			}
		});

		// to logurl2
		// TODO : 

		/*
		wx.request({
			url    : 'https://api3tclass.3ttech.cn/edu/exception',
			method : 'POST',
			data   : {
				message : msg,
				type    : 'miniappsdk',
				level   : logLevel,
				classId : roomId,
				UID     : userId
			},
			header : {
				'content-type' : 'application/x-www-form-urlencoded;charset=UTF-8'
			},
			success(res)
			{
			}
		});
		*/

		// 
		/* eslint-disable no-console */
		if (logLevel < this._logLevel)
		{
			console.log(`time=${new Date()} ${msg}`);
		}
		/* eslint-enable no-console */
	}
}

module.exports = Logger;
