const Logger = require('../utils/Logger.js');
const EnhancedEventEmitter = require('../utils/EnhancedEventEmitter.js');
const Message = require('./Message.js');
const WebSocketTransport = require('./transports/WebSocketTransport.js');

import regeneratorRuntime from '../thirdparty/runtime.js';
import { stringify } from 'querystring';

class Peer extends EnhancedEventEmitter
{
	/**
	 * @param {String} protooUrl -- url
	 * @param {String} logClues -- 
	 *
	 * @emits open
	 * @emits {currentAttempt: Number} failed
	 * @emits disconnected
	 * @emits close
	 * @emits {request: protoo.Request, accept: Function, reject: Function} request
	 * @emits {notification: protoo.Notification} notification
	 */
	constructor(protooUrl, logClues, logger)
	{
		super(logger);

		// 
		this._logger = logger || new Logger('Peer');
		this._logger.prefix = '-';

		// 
		this._logClues = logClues;

		// Closed flag.
		// @type {Boolean}
		this._closed = false;

		// Transport.
		// @type {protoo.Transport}
		this._transport = null;

		// Connected flag.
		// @type {Boolean}
		this._connected = false;

		// Custom data object.
		// @type {Object}
		this._data = {};

		// Map of pending sent request objects indexed by request id.
		// @type {Map<Number, Object>}
		this._sents = new Map();

		// 
		this._protooUrl = protooUrl;
	}

	init()
	{
		this._transport = new WebSocketTransport(
			this._protooUrl,
			this._logClues,
			this._logger,
			null);

		this._transport.init();

		// ws 
		this._transport.on('open', (event) =>
		{
			if (this._closed)
				return;

			const {
				isRetry
			} = event;
			
			this._connected = true;

			this.safeEmit('open', {
				isRetry
			});
		});

		// 
		this._transport.on('auto-reconnecting', () =>
		{
			if (this._closed)
				return;

			this._connected = false;

			this.safeEmit('auto-reconnecting');
		});

		// ws / -- N
		this._transport.on('failed', () =>
		{
			if (this._closed)
				return;

			//  closeTransport --  WebsocketTransport  failed close
			// this.closeTransport();
			this._connected = false;

			this.safeEmit('failed');
		});

		// ws  -- 
		this._transport.on('close', (e) =>
		{
			if (this._closed)
				return;

			//  closeTransport --  WebsocketTransport  failed close
			// this.closeTransport();
			this._connected = false;

			this.safeEmit('close', e);
		});

		//  message  Object  --  JSON.parse(raw) 
		this._transport.on('message', (event) =>
		{
			const {
				message
			} = event;

			if (message.request)
			{
				this._handleRequest(message);
			}
			else if (message.response)
			{
				this._handleResponse(message);
			}
			else if (message.notification)
			{
				this._handleNotification(message);
			}
		});
	}

	/**
	 * Whether the Peer is closed.
	 *
	 * @returns {Boolean}
	 */
	get closed()
	{
		return this._closed;
	}

	/**
	 * Whether the Peer is connected.
	 *
	 * @returns {Boolean}
	 */
	get connected()
	{
		return this._connected;
	}

	/**
	 * App custom data.
	 *
	 * @returns {Object}
	 */
	get data()
	{
		return this._data;
	}

	/**
	 * Invalid setter.
	 */
	set data(data) // eslint-disable-line no-unused-vars
	{
		throw new Error('cannot override data object');
	}

	// 
	closeTransport(e)
	{
		this._connected = false;

		// Close Transport.
		this._transport.close(e);

		// Close every pending sent.
		for (const sent of this._sents.values())
		{
			sent.close();
		}
	}

	/**
	 * Close this Peer and its Transport.
	 */
	close()
	{
		this._logger.debug('Peer API close() OK');

		if (this._closed)
			return;

		this._closed = true;

		this.closeTransport({
			code   : 1000,
			reason : 'client close connection normally.'
		});
	}

	// 
	resumeProcess()
	{
		if (Boolean(this._transport))
		{
			this._transport.resumeProcess();
		}
	}

	// 
	suspendProcess()
	{
		if (Boolean(this._transport))
		{
			this._transport.suspendProcess();
		}
	}

	// 
	reconnect(e)
	{
		if (Boolean(this._transport))
		{
			this._transport.reconnect(e);
		}
	}

	/**
	 * Send a protoo request to the server-side Room.
	 *
	 * @param {String} method
	 * @param {Object} [data]
	 *
	 * @async
	 * @returns {Object} The response data Object if a success response is received.
	 */
	async request(method, data = undefined)
	{
		const request = Message.createRequest(method, data);

		this._logger.debug(`Peer API request() [method: ${method} request.id: ${request.id}]`);

		// This may throw.
		this._transport.send(request);

		return new Promise((resolve, reject) =>
		{
			const timeout = 1500 * (15 + (0.1 * this._sents.size));
			const sent =
			{
				id      : request.id,
				method  : request.method,
				resolve : (data2) =>
				{
					this._logger.debug(`Peer API request() request[method: ${method} request.id: ${request.id}] <RESOLVE>`);

					if (!this._sents.delete(request.id))
						return;

					clearTimeout(sent.timer);
					
					resolve(data2);
				},
				reject : (error) =>
				{
					this._logger.debug(`Peer API request() request[method: ${method} request.id: ${request.id}] <REJECT>`);

					if (!this._sents.delete(request.id))
						return;

					clearTimeout(sent.timer);
					reject(error);
				},
				timer : setTimeout(() =>
				{
					this._logger.debug(`Peer API request() request[method: ${method} request.id: ${request.id}] <TIMEOUT>`);

					if (!this._sents.delete(request.id))
						return;

					reject(new Error('Request Timeout.'));
				}, timeout),
				close : () =>
				{
					clearTimeout(sent.timer);
					reject(new Error('Peer Closed'));
				}
			};

			// Add sent stuff to the map.
			this._sents.set(request.id, sent);
		});
	}

	/**
	 * Send a protoo notification to the server-side Room.
	 *
	 * @param {String} method
	 * @param {Object} [data]
	 *
	 * @async
	 */
	async notify(method, data = undefined)
	{
		const notification = Message.createNotification(method, data);

		this._logger.debug(`Peer API notify() [method: ${method}]`);

		// This may throw.
		this._transport.send(notification);
	}

	_handleRequest(request)
	{
		try
		{
			this.emit('request',
				// Request.
				request,
				// accept() function.
				(data) =>
				{
					const response = Message.createSuccessResponse(request, data);

					this._transport.send(response)
						.catch(() => {});
				},
				// reject() function.
				(errorCode, errorReason) =>
				{
					if (errorCode instanceof Error)
					{
						errorCode = 500;
						errorReason = String(errorCode);
					}
					else if (typeof errorCode === 'number' && errorReason instanceof Error)
					{
						errorReason = String(errorReason);
					}

					const response = Message.createErrorResponse(request, errorCode, errorReason);

					this._transport.send(response)
						.catch(() => {});
				});
		}
		catch (error)
		{
			const response = Message.createErrorResponse(request, 500, String(error));

			this._transport.send(response)
				.catch(() => {});
		}
	}

	_handleResponse(response)
	{
		const sent = this._sents.get(response.id);

		if (!sent)
		{
			this._logger.error(
				`Peer _handleResponse() response [id: ${response.id}] doesnot match any request`);

			return;
		}

		if (response.ok)
		{
			sent.resolve(response.data);
		}
		else
		{
			const error = new Error(response.errorReason);

			error.code = response.errorCode;
			sent.reject(error);
		}
	}

	_handleNotification(notification)
	{
		this.safeEmit('notification', notification);
	}
}

module.exports = Peer;
