/**
 * Generates a random positive integer.
 *
 * @returns {Number}
 */
exports.generateRandomNumber = function ()
{
	return Math.round(Math.random() * 10000000);
};

exports.guid = function ()
{
	function S4()
	{
		return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
	}

	const s1 = S4();
	const s2 = S4();
	const s3 = S4();
	const s4 = S4();
	const s5 = S4();
	const s6 = S4();
	const s7 = S4();
	const s8 = S4();

	return (`${s1}${s2}-${s3}-${s4}-${s5}-${s1}${s1}${s1}`);
	//	return (S4()+S4()+"-"+S4()+"-"+S4()+"-"+S4()+"-"+S4()+S4()+S4());
};

// ^((https|http|ftp|rtmp|rtsp|mms)?://)?(([0-9a-z_!~*\'().&=+$%-]+: )?[0-9a-z_!~*\'().&=+$%-]+@)?(([0-9]{1,3}.){3}[0-9]{1,3}|([0-9a-z_!~*\'()-]+.)*([0-9a-z][0-9a-z-]{0,61})?[0-9a-z].[a-z]{2,6})(:[0-9]{1,4})?((/?)|(/[0-9a-z_!~*\'().;?:@&=+$,%#-]+)+/?)$
exports.isURL = function (url)
{
	const strRegex = '^((https|http|ftp|rtmp|rtsp|mms)?://)'
		+ '?(([0-9a-zA-Z_!~*\'().&=+$%-]+: )?[0-9a-zA-Z_!~*\'().&=+$%-]+@)?' //ftpuser@ 
		+ '(([0-9]{1,3}.){3}[0-9]{1,3}' // IPURL- 199.194.52.184 
		+ '|' // IPDOMAIN 
		+ '([0-9a-zA-Z_!~*\'()-]+.)*' // - www. 
		+ '([0-9a-zA-Z][0-9a-zA-Z-]{0,61})?[0-9a-zA-Z].' //  
		+ '[a-zA-Z]{2,6})' // first level domain- .com or .museum 
		+ '(:[0-9]{1,4})?' // - :80 
		+ '((/?)|' // a slash isn't required if there is no file name 
		+ '(/[0-9a-zA-Z_!~*\'().;?:@&=+$,%#-]+)+/?)$';
	const re = new RegExp(strRegex);
	
	if (re.test(url))
	{
		return (true);
	}
	else
	{
		return (false);
	}
};
