const WxWebSocket = require('../../utils/WxWebSocket.js');
const retry = require('../../retry/retry.js');
const Logger = require('../../utils/Logger.js');
const EnhancedEventEmitter = require('../../utils/EnhancedEventEmitter.js');
const Message = require('../Message.js');

import regeneratorRuntime from '../../thirdparty/runtime.js';

//
const WST_FULL_TIMEOUT = 45000; // 45s

// 
const MAX_RETRY = 4;

// 
// {
const WST_STATE_IDLE = 0; // /
const WST_STATE_TRYING = 1; // 
const WST_STATE_ACT = 2; // /
const WST_STATE_RETRYING = 3; // 
const WST_STATE_FINAL_FAIL = 4; // 
// }

// const WS_SUBPROTOCOL = 'protoo';

//  / retry
const DEFAULT_RETRY_OPTIONS =
{
	retries    : MAX_RETRY, // 
	factor     : 2, // 
	minTimeout : 1 * 1000, // 
	maxTimeout : 4 * 1000
};

class WebSocketTransport extends EnhancedEventEmitter
{
	/**
	 * @param {String} url       -- WebSocket URL.
	 * @param {String} logClues  -- 
	 * @param {Object} [options] -- Options for WebSocket-Node.W3CWebSocket and retry.
	 */
	constructor(url, logClues, logger, options)
	{
		super(logger);

		// 
		this._logger = logger || new Logger('WebSocketTransport');
		this._logger.prefix ='-';

		// 
		this._logClues = logClues;

		//  close ?
		this._stopping = false;

		//   
		this.wstTimer = null;

		// 
		this._wstState = WST_STATE_IDLE;

		// WebSocket URL.
		// @type {String}
		//  url  ''
		this._url = `${url}&logClues=${this._logClues}`;

		// Options.
		// @type {Object}
		this._options = options || {};

		// WebSocket instance.
		// @type {WebSocket}
		this._ws = null;

		this._operation = null;
		
		//  / 
		this._suspend = false;

		// 
		this._randomId = 0;
	}

	init()
	{
		if (this._wstState !== WST_STATE_IDLE)
		{
			this._logger.error(`WebSocketTransport init() -- error: this._wstState(${this._wstState}) !== WST_STATE_IDLE. return`);

			return;
		}
		try
		{
			// 
			this._stopping = false;

			this._wstState = WST_STATE_TRYING;
			
			this.startTransport({
				reason : 'init -- startTransport'
			});
		}
		catch (error)
		{
			this._logger.error(`WebSocketTransport init() EXCEPTION!!! -- error: ${JSON.stringify(error)}`);
			throw new Error(error);
		}
	}

	get closed()
	{
		return (this._wstState === WST_STATE_IDLE || this._wstState === WST_STATE_FINAL_FAIL);
	}

	close(e)
	{
		let code = 1000;
		let reason = 'close -- client close connection normally';

		if (Boolean(e))
		{
			code = e.code;
			reason = e.reason;
		}

		this._logger.debug(`WebSocketTransport close() -- this._randomId: ${this._randomId}`);

		if (this.closed)
			return;

		// 
		this._stopping = true;

		// Don't wait for the WebSocket 'close' event, do it now.

		this.closeTransport({
			code,
			reason
		});

		// 
		this._operation.stop();
		// 
		this._wstState = WST_STATE_IDLE;
	}

	// 
	resumeProcess()
	{
		this._suspend = false;
	}

	// 
	suspendProcess()
	{
		this._suspend = true;
	}

	// 
	startTransport(e)
	{
		// 
		/*
		if (this._suspend)
		{
			this._logger.debug('WebSocketTransport startTransport() system suspend!!!');

			return;
		}
		*/

		this._logger.debug('WebSocketTransport startTransport()');

		// 
		const wstProc = new Promise((resolve, reject) =>
		{
			this._logger.error('WebSocketTransport startTransport() start WebSocket...');

			this._operation = retry.operation(this._options.retry || DEFAULT_RETRY_OPTIONS);
	
			if (!this._operation)
			{
				this._logger.error('WebSocketTransport startTransport() Internal error.');
	
				reject('Internal error');
			}
			else
			{
				// 
				this.attemptConnect(e);
			}
		});

		const wstTimerPromise = new Promise((resolve, reject) =>
		{
			const wsTransport = this;

			this.wstTimer = setTimeout(() =>
			{
				if (wsTransport._wstState !== WST_STATE_ACT)
				{
					wsTransport._logger.debug(`WebSocketTransport wstTimerPromise TIMEOUT -- wsTransport._wstState(${wsTransport._wstState}) !== WST_STATE_ACT. reject(WebSocketTransport init timeout)`);

					reject({
						code   : 500,
						reason : 'WebSocketTransport init timeout'
					});
				}
				else
				{
					wsTransport._logger.debug('WebSocketTransport wstTimerPromise TIMEOUT -- ignore');
				}
			}, WST_FULL_TIMEOUT);
		});

		Promise.race([ wstProc, wstTimerPromise ])
			.then((result) =>
			{
			})
			.catch((event) =>
			{
				//   ACT return
				if (this._wstState === WST_STATE_ACT)
				{
					return;
				}
				else
				{
					// {
					this.closeTransport({
						code   : 500,
						reason : 'WebSocketTransport FINAL FAILED'
					});

					this._operation.stop();
					// 
					this._wstState = WST_STATE_FINAL_FAIL;
					// }

					this._logger.debug('WebSocketTransport startTransport() -- emit [failed] WebSocketTransport FINAL FAILED');
					// 
					this.safeEmit('failed', {
						code   : 500,
						reason : 'WebSocketTransport FINAL FAILED'
					});
				}
			});
		// 
	}

	// 
	closeTransport(opts)
	{
		let code = 1000;
		let reason = 'close connection';

		if (Boolean(opts))
		{
			code = opts.code;
			reason = opts.reason;
		}

		if (Boolean(this._ws))
		{
			this._logger.debug(`WebSocketTransport closeTransport() -- code: ${code} reason: ${reason}`);

			try
			{
				this._ws.close({
					code,
					reason
				});
			}
			catch (error)
			{
				this._logger.error(`WebSocketTransport closeTransport() -- _ws.close EXCEPTION: ${error.message}`);
			}
		}
	}

	// 
	reconnect(e)
	{
		this._logger.debug('WebSocketTransport reconnect() -- will reconnect...');
		// 
		this._operation.stop();
		// 
		this._wstState = WST_STATE_RETRYING; // 

		// 
		this.safeEmit('auto-reconnecting');

		this.startTransport(e);
	}

	async send(message)
	{
		if (this._wstState !== WST_STATE_ACT)
		{
			this._logger.warn('WebSocketTransport send() -- error: transport no-connection');
			throw new Error('transport no-connection');
		}

		try
		{
			const msg = JSON.stringify(message);

			this._logger.warn(`WebSocketTransport send() <<<<<< ${msg}`);

			if (Boolean(this._ws))
			{
				this._ws.send({ msg });
			}
		}
		catch (error)
		{
			this._logger.warn(`WebSocketTransport send() -- EXCEPTION: ${error.message}`);

			throw error;
		}
	}

	// 
	// 
	onWsOpen()
	{
		if (Boolean(this._operation))
		{
			this._operation.stop();
		}

		// 
		//  close  return
		if (this._stopping)
		{
			this._logger.debug(`WebSocketTransport onWsOpen() -- this._randomId: ${this._randomId} -- this._stopping is ture, return.`);

			return;
		}

		// 
		if (this.wstState === WST_STATE_FINAL_FAIL)
		{
			this._logger.warn('WebSocketTransport onWsOpen() -- This has been WST_STATE_FINAL_FAIL, so return.');

			return;
		}

		this._logger.debug(`WebSocketTransport onWsOpen() -- this._randomId: ${this._randomId} -- will emit [open]`);

		// 
		const isRetry = (this._wstState === WST_STATE_RETRYING);

		this._wstState = WST_STATE_ACT;

		// 
		if (this.wstTimer !== null)
		{
			clearTimeout(this.wstTimer);
			this.wstTimer = null;
		}

		// Emit 'open' event.
		this.safeEmit('open', {
			isRetry
		});
	}
	
	onWsMessage(data)
	{
		if (this._wstState !== WST_STATE_ACT)
		{
			this._logger.debug(`WebSocketTransport onWsMessage() -- message: ${data} -- this._wstState(${this._wstState}) !== WST_STATE_ACT, return.`);

			return;
		}

		const message = Message.parse(data);

		if (!message)
		{
			this._logger.debug('WebSocketTransport onWsMessage() -- message is null, return');

			return;
		}

		if (this.listenerCount('message') === 0)
		{
			this._logger.error('WebSocketTransport onWsMessage() -- no listeners for event [message], ignoring message');

			return;
		}
		
		this._logger.warn(`WebSocketTransport onWsMessage() >>>>>> ${JSON.stringify(message)}`);

		// Emit 'message' event.
		this.safeEmit('message', { message });
	}
	
	// 
	onWsError(event)
	{
		if (this._wstState === WST_STATE_IDLE)
			return;

		this._logger.error(`WebSocketTransport onWsError() -- this._wstState: ${this._wstState} error: ${JSON.stringify(event)}`);
	}

	// 
	// N ""
	//  "close"
	onWsClose(opts)
	{
		const { event } = opts;

		/*
		wx.setStorage({
			key  : `WebSocketTransport-${new Date().getTime()}`,
			data : `WebSocketTransport event[onWsClose] currentAttempt: ${currentAttempt} event: ${JSON.stringify(event)}.`
		});
		*/

		//  IDELFIANL_FAIL   onWsClose 
		if (this._wstState === WST_STATE_IDLE || this._wstState === WST_STATE_FINAL_FAIL)
		{
			this._logger.warn(`WebSocketTransport onWsClose -- Invalid _wstState(${this._wstState}), so return.`);

			return;
		}

		// 
		this._logger.debug(`WebSocketTransport onWsClose() -- this._wstState: ${this._wstState} ${JSON.stringify(event)}`);

		// Don't retry when in the following cases:
		// 4000 : closed by server
		// 4100 : 
		// _stopping : 
		if (event.code === 4000 || event.code === 4100 || this._stopping)
		{
			// 
			this._logger.debug(`WebSocketTransport onWsClose() -- [event: ${JSON.stringify(event)}]. Normally closed. No more attempt to reconnect!`);

			// 
			if (Boolean(this._operation))
			{
				this._operation.stop();
			}
			// 
			//  close / --  close  close()
			/* 2019.7.18 wxwebsocketcloseTransport
			this.closeTransport({
				code   : 1000,
				reason : `onWsClose[event: ${event.code}] -- close connection`
			});
			*/

			this._wstState = WST_STATE_FINAL_FAIL;

			//  'close' 
			// Emit 'close' event.
			this.safeEmit('close', {
				code   : event.code,
				reason : this._stopping ? '_stopping' : `Normally closed -- code: ${JSON.stringify(event)}`
			});

			return;
		}
		// 
		else
		{
			// 
			//  _operation  retry
			this.safeEmit('auto-reconnecting');

			// 
			if (this._wstState === WST_STATE_ACT)
			{
				//      attemptConnect
				this._logger.debug('WebSocketTransport onWsClose() -- this._wstState !== WST_STATE_RETRYING, A new beginning...');

				//  operation  attempt
				if (Boolean(this._operation))
				{
					this._operation.reset();
				}

				//
				this._wstState = WST_STATE_RETRYING; // 

				setTimeout(() =>
				{
					this.attemptConnect({
						reason : 'onWsClose -- retrying'
					});
				});

				return;
			}
		}

		/*
		else if (this._wstState === WST_STATE_RETRYING) //  retying  retry
		{
			this.operationRetry(event);
		}
		*/
	}

	attemptConnect(e)
	{
		if (!this._operation)
		{
			this._logger.error('WebSocketTransport attemptConnect() this._operation is null.');

			return;
		}

		this._operation.attempt((currentAttempt) =>
		{
			// 
			if (this._stopping)
			{
				if (Boolean(this._operation))
				{
					this._operation.stop();
				}

				this.closeTransport({
					code   : 1000,
					reason : 'attemptConnect _stopping -- client close connection normally'
				});

				this._logger.debug('WebSocketTransport attemptConnect() -- _stopping is true, has closeTransport.');

				return;
			}

			// 
			const commentReason = (!e ? '' : e.reason);

			// TODO : ??? 
			// closeTransport  ???
			this.closeTransport({
				code   : 3000,
				reason : `Will retry later -- ${commentReason}`
			});

			// 
			this._logger.debug(`WebSocketTransport attemptConnect() -- [currentAttempt: ${currentAttempt}]`);

			// 
			this._randomId = Math.round(Math.random() * 10000000);

			this._ws = new WxWebSocket(this._randomId, this._logger);

			if (Boolean(this._ws))
			{
				//  wx websocket 
				this._ws.on('open', (event) =>
				{
					if (event.randomId !== this._randomId)
					{
						// 
						/*
						wx.setStorage({
							key  : `WebSocketTransport-${new Date().getTime()}`,
							data : `WebSocketTransport _ws event[open] randmId un-match ${event.randomId} ${this._randomId}.`
						});
						*/
						this._logger.warn(`WebSocketTransport _ws event[open] randmId un-match ${event.randomId} ${this._randomId}.`);
					}
					else
					{
						// 
						this.onWsOpen();
					}
				});

				this._ws.on('message', (event) =>
				{
					if (event.randomId !== this._randomId)
					{
						this._logger.warn(`WebSocketTransport _ws event[message] randmId un-match ${event.randomId} ${this._randomId}.`);
					}
					else
					{
						this.onWsMessage(event.data);
					}
				});

				this._ws.on('error', (event) =>
				{
					if (event.randomId !== this._randomId)
					{
						// 
						/*
						wx.setStorage({
							key  : `WebSocketTransport-${new Date().getTime()}`,
							data : `WebSocketTransport _ws event[error] randmId un-match ${event.randomId} ${this._randomId}.`
						});
						*/

						this._logger.warn(`WebSocketTransport _ws event[error] randmId un-match ${event.randomId} ${this._randomId}.`);
					}
					else
					{
						// 
						this.onWsError(event);
					}
				});

				this._ws.on('close', (event) =>
				{
					if (event.randomId !== this._randomId)
					{
						// 
						/*
						wx.setStorage({
							key  : `WebSocketTransport-${new Date().getTime()}`,
							data : `WebSocketTransport _ws event[close] randmId un-match ${event.randomId} ${this._randomId}.`
						});
						*/

						this._logger.warn(`WebSocketTransport _ws event[close] randmId un-match ${event.randomId} ${this._randomId}.`);
					}
					else
					{
						// 
						this.onWsClose({ event });
					}
				});

				// connect server
				this._ws.connect({
					url     : (this._wstState === WST_STATE_RETRYING ? (`${this._url}&retry=true`) : this._url),
					success : (res) =>
					{
					},
					fail : (res) =>
					{
						/*
						wx.setStorage({
							key  : `WebSocketTransport-${new Date().getTime()}`,
							data : 'WebSocketTransport _ws.connect fail.'
						});
						*/
	
						this._logger.error(`_ws.connect failed. [currentAttempt: ${currentAttempt}]`);

						throw new Error('_ws.connect failed');
					}
				});
			}
			else
			{
				this._logger.error(`WebSocket create failed -- _ws is null. [currentAttempt: ${currentAttempt}]`);

				throw new Error('WebSocket create failed -- _ws is null');
			}
		}, {
			timeout : 10 * 1000, // 8s 
			cb      : (ctr) =>
			{
				this._logger.debug(`attemptConnect() _operation.attempt timeout!! [retry: ${ctr}]. _wstState is ${this._wstState}`);

				if (this._wstState === WST_STATE_ACT)
				{
					return;
				}
				else if (this._wstState === WST_STATE_TRYING || this._wstState === WST_STATE_RETRYING)
				{
					this._logger.error(`attemptConnect() _operation.attempt timeout(10s). [retry: ${ctr}]. continue to retry...`);
					// 
					setTimeout(() =>
					{
						this.operationRetry({
							code : 3100
						});
					});
				}
			}
		});
	}

	operationRetry(event)
	{
		if (!this._operation)
		{
			this._logger.error('WebSocketTransport operationRetry() -- error: this._operation is null.');

			return;
		}
		//

		if (this._operation.retry(true))
		{
			/*
			wx.setStorage({
				key  : `WebSocketTransport-${new Date().getTime()}`,
				data : `WebSocketTransport operationRetry() ok. event: ${JSON.stringify(event)}.`
			});
			*/

			this._logger.debug(`WebSocketTransport operationRetry() -- _operation.retrying [retry: ${this._operation._attempts}] [event.code: ${event.code}]`);

			// 
			this.safeEmit('auto-reconnecting');

			return;
		}
		else
		{
			/*
			wx.setStorage({
				key  : `WebSocketTransport-${new Date().getTime()}`,
				data : 'WebSocketTransport operationRetry() FINAL FAILED.'
			});
			*/
			
			this._logger.error(`WebSocketTransport operationRetry() -- _operation.retry end up [retry: ${this._operation_attempts}]. FINAL FAILED`);

			// 
			this._operation.stop();
			// 
			this._wstState = WST_STATE_FINAL_FAIL;
			//  ok failed event:{code: 500}
			this.safeEmit('failed', {
				code   : 500,
				reason : 'WebSocketTransport FINAL FAILED'
			});
		}
	}
}

module.exports = WebSocketTransport;
